








































import Vue from "vue";
import { Component } from "vue-property-decorator";
import moment from "moment";

@Component
export default class NavBar extends Vue {
  scrollIntoView(classNameOfSection: string) {
    const element = document.getElementsByClassName(classNameOfSection)[0];
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
  }

  theGameIsReleased(): boolean {
    const releaseDate = moment("2021-11-22");
    const currentDate = moment();
    return (
      releaseDate.isSame(currentDate, "day") ||
      currentDate.isAfter(releaseDate, "day")
    );
  }
}
